.page-footer {
  // background-color: color("color-1", "darken-1");

  .col {
    padding-left: 0;
    padding-right: 0;
  }

  .footer-title {
    font-size: 18px;
    img {
      width: 60px;
      margin-left: 2px;
    }
  }
  .footer-text {
    font-size: 14px;
    margin-top: 0px;
    margin-bottom: 8px;
    color: #ffffff;
    b {
      font-weight: 800;
    }
    &:first-child {
      margin-top: 10px;
      @media #{$large-and-up} {
        margin-top: 20px;
      }
    }
  }

  a {
    color: color("color-1", "sub");
  }

  .footer-copyright {
    font-size: 14px;
    color: rgba(#ffffff, 0.85);
  }
}
