// Animation
@mixin fade($duration, $delay) {
  opacity: 0;
  animation: fadeIn ease $duration;
  animation-delay: $delay;
  animation-fill-mode: both;
}

@mixin fadeRight($duration, $delay) {
  opacity: 0;
  animation: fadeRight ease $duration;
  animation-delay: $delay;
  animation-fill-mode: both;
}

@mixin fadeBottom($duration, $delay) {
  opacity: 0;
  animation: fadeBottom ease $duration;
  animation-delay: $delay;
  animation-fill-mode: both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeRight {
  from {
    opacity: 0;
    transform: translate3d(10%, 0, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeBottom {
  from {
    opacity: 0;
    transform: translate3d(0, 20%, 0);
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
