#pageMain {
  .service {
    padding: 30px 0;
  }

  .main-section-1 {
    padding: 0;

    @media #{$medium-and-up} {
    }

    .main-slider {
      .container {
        display: flex;
        flex-flow: column;
        flex: 1 0 auto;
        align-items: center;
        justify-content: center;
        height: 100%;
        text-align: center;
      }
      img {
        width: 100%;
      }
    }

    h1 {
      font-weight: 700;
    }
    h5 {
      font-weight: 100;
      line-height: 1.5;
    }
  }

  .main-section-2 {
    .service {
      display: flex;
      justify-content: space-between;
      text-align: center;
      flex: 1 1 auto;

      a {
        display: flex;
        flex-flow: column;
        justify-content: flex-start;
        align-items: center;
        flex: 1;
        padding: 20px;
        margin-right: 10px;
        border-radius: 4px;
        color: #242424;
        font-weight: 700;
        font-size: 15px;
        background-color: color("grey", "lighten-3");

        i {
          margin-bottom: 6px;
          font-size: 40px;
        }

        &.box1 {
        }
        &.box2 {
        }
        &.box3 {
          margin-right: 0;
          i {
            margin-bottom: 16px;
          }
        }
        img {
          width: 60px;
        }
      }
    }
  }

  .main-section-3 {
    background-color: color("grey", "lighten-5");
    .service {
      display: flex;
      justify-content: space-between;
      flex: 1;

      a {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        flex: 1 1 auto;
        padding: 20px;
        margin-right: 10px;
        border-radius: 4px;
        font-weight: 700;
        font-size: 15px;
        border: 1px solid #ccc;
        color: #242424;
        background-color: #fff;
        border-left: 4px solid color("color-1", "sub");

        &.box1 {
        }
        &.box2 {
        }
        &.box3 {
          margin-right: 0;
        }
      }
    }
  }
}
