.btn {
  font-weight: 700;
  border-radius: 3px;
  height: 60px;
  line-height: 60px;
  background-color: color("color-1", "base");
  color: #fff;
}

.btn-primay {
  background-color: color("color-1", "lighten-1");
}

.btn-primay-light {
  background-color: color("color-1", "lighten-2");
}

.btn-primay-darken-2 {
  background-color: color("color-1", "darken-2");
}

.btn-sub {
  background-color: color("color-1", "sub");
  color: color("grey", "darken-4");
}

.btn-sub-darken {
  background-color: color("color-1", "darken-1");
  color: color("color-1", "darken-2");
}

.btn-grey,
.btn-cancle {
  background-color: color("grey", "lighten-2");
  color: color("color-1", "darken-2");
}

.btn-signup {
  background-color: color("color-1", "lighten-1");
}

.btn-login {
  width: 90%;
}

.btn-goto {
  height: 60px;
  line-height: 60px;
  width: 90%;
  margin: 0 auto;
}

.sign-main {
  .btn {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    height: auto !important;
    line-height: 1.5 !important;
    font-size: 24px;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2) !important;

    span {
      display: block;
      font-size: 18px;
      font-weight: 300;
    }
  }
}

.payment-text {
  text-align: right;
  display: block;
  font-size: 14px;
}

.btn-next {
  display: block;
  width: 100%;
  color: #fff;
  background-color: color("color-1", "darken-2");
  height: 60px;
  line-height: 60px;
}

.table-btn-wrap {
  display: flex;
  justify-content: space-between;
  margin-top: 1.5rem;

  .btn {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    border-radius: 3px;
    font-weight: 500;

    &-pay-cancle {
      margin-right: auto;
      background: color("red", "darken-1");
    }
    &-email {
      background: color("color-1", "base");
    }
    &-back {
      margin-left: 10px;
      color: color("grey", "darken-1");
      background: color("grey", "lighten-3");
    }
  }
}

.btn-block {
  display: block;
}

