html {
  height: 100%;
}

body {
  word-break: keep-all;
  font-family: "Spoqa Han Sans Neo", "", malgun gothic, "", Dotum,
    "Apple SD Gothic Neo", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 400;
}

p {
  color: color("grey", "darken-1");
}

main {
  margin-top: $navbar-height-mobile;
  @media #{$medium-and-up} {
    margin-top: $navbar-height;
  }
}

