.btn {
  box-shadow: none !important;
}

.block {
  margin-bottom: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.25;
  font-weight: 700;
}

.img-list {
  li {
    position: relative;
    padding-left: 40px;
    margin-bottom: 10px;

    &::before {
      content: "";
      background-image: url("../images/icon/list-img.png");
      display: inline-block;
      width: 34px;
      height: 34px;
      background-size: 34px;
      background-repeat: no-repeat;
      background-position: 0 -4px;
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}

.img-list-2 {
  li {
    position: relative;
    padding-left: 40px;
    margin-bottom: 10px;

    img {
      display: inline-block;
      width: 34px;
      position: absolute;
      left: 0;
      top: -4px;
    }
  }
}
.border-top {
  border-top: 1px solid $table-border-color;
}

.modal {
  max-width: 1000px;
  .modal-box {
    border-top: 1px solid $table-border-color;
    ul {
      li {
        margin-bottom: 10px;
      }
    }
    p {
      color: #000000;
    }
  }
  .modal-footer {
    border-radius: 0 0 2px 2px;
    background-color: #fafafa;
    padding: 4px 6px;
    height: 66px;
    width: 100%;
    text-align: right;
  }
}

.fw {
  &-100 {
    font-weight: 100 !important;
  }
  &-300 {
    font-weight: 300 !important;
  }
  &-400 {
    font-weight: 400 !important;
  }
  &-500 {
    font-weight: 500 !important;
  }
  &-700 {
    font-weight: 700 !important;
  }
}

// Section grid
.section {
  padding: 30px 0;

  .content-wrap {
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    padding: 3rem 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-flow: column;
    flex-flow: column;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    height: 100%;
    min-height: 100%;
  }
}
@media #{$medium-and-down} {
  .row {
    padding-left: 0;
    padding-right: 0;
    .col.first {
      padding-left: 0;
    }
    .col.center {
      padding-left: 0.375rem;
      padding-right: 0.375rem;
    }
    .col.last {
      padding-right: 0;
    }
    .col.s12 {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

#pageCms {
  .section {
    padding: 30px 0 30px 0;

    .container {
      max-width: 600px;
    }
  }
}
.info-box {
  background-color: color("grey", "lighten-5");
  padding: 20px 20px;
  margin-bottom: 10px;
  color: color("grey", "darken-3");
  ul {
    margin: 0;
    li {
      font-size: 16px;
      display: flex;
      justify-content: space-between;
      line-height: 30px;
      b {
        width: 70px;
        font-weight: 500;
        flex: 1 1 auto;
        font-weight: 500;
      }
      span {
        font-weight: 300;
      }
    }
  }

  &.list {
    ul {
      li {
        color: color("grey", "darken-1");
        margin-bottom: 6px;
        padding-left: 16px;
        position: relative;

        &:last-child {
          margin-bottom: 0;
        }

        &::before {
          content: "";
          display: inline-block;
          width: 6px;
          height: 6px;
          border-radius: 6px;
          position: absolute;
          top: 10px;
          left: 0px;
          background: color("grey", "lighten-1");
        }
      }
    }
  }
}

.warring-text {
  font-size: 14px;
  letter-spacing: -0.5px;
  color: $error-color;
}

.cms-form {
  margin-top: 60px;

  .switch {
    margin-bottom: 10px;

    .flex-switch {
      font-size: 16px;
      color: #777;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}

.w100 {
  width: 100%;
}

.sign-form {
  .input-field {
    .helper-text {
      position: absolute;
      bottom: 9px;
      left: 16px;
      padding: 0 6px;
      width: 100%;
    }
  }
  .text {
    font-size: 14px;
    color: color("grey", "lighten-1");
  }
  .col-block {
    span {
      display: block;
      margin-bottom: 0.5rem;
    }
    .form-row {
      margin-bottom: 0;

      .input-field {
        margin-top: 0;
        margin-bottom: 0;
        &.col {
          &:first-child {
            padding-left: 0;
          }
          &:last-child {
            padding-right: 0;
          }
          &.center {
            font-size: 30px;
            height: 4rem;
            line-height: 4rem;
            margin-bottom: 0;
          }
          label {
            left: 16px;
          }
          &.email {
            input {
              padding-right: 3rem;
            }
            .label-icon {
              position: absolute;
              right: 28px;
              top: 16px;
              font-size: 20px;
              color: color("grey", "lighten-1");
              margin-bottom: 0;
            }
          }
        }
      }
    }

    .btn {
      width: 100%;
      border-radius: 4px;
      height: $input-height;
      font-size: 16px;
      line-height: 1.25;
      margin-top: 0.25rem;
    }
  }

  .file-field {
    .btn {
      width: 100px;
      border-radius: 4px;
      background-color: color("color-1", "darken-2");
      font-size: 14px;
      padding: 0;
      // word-break: keep-all;
      span {
        display: block;
        line-height: 1.25;
        padding-top: 16px;
      }
    }
  }

  .file-camera {
    background-image: url(../images/icon/icon-camera.png);
    background-repeat: no-repeat;
    background-position: 92% 14px;
    background-size: 36px;

    @media only screen and (min-width: $medium-screen) {
      background-position: 97% 18px;
    }

    @media only screen and (min-width: $large-screen) {
      background-position: 97% 22px;
    }
  }
}
.btn-wrap {
  margin-top: 3rem;
  .btn {
    width: 100%;
    height: 4rem;
    line-height: 4rem;
    margin-bottom: 10px;
  }
}

.search-res {
  text-align: right;
  margin-top: 10px;
  span {
    font-size: 12px;
    color: color("grey", "darken-1");
  }
}

