.materialize-textarea {
  padding: 20px 16px !important;
}

//  pageBoard
.board-link-list {
  padding: 30px 0px;
  text-align: center;
  li {
    margin-bottom: 20px;

    .link {
      display: block;
      padding: 1.5rem 1rem;
      font-size: 24px;
      font-weight: 500;
      color: #fff;
      border-radius: 4px;
      background-color: color("color-1", "base");
    }
  }
}

.policy-link-list {
  padding: 30px 0px;
  li {
    margin-bottom: 20px;

    .link {
      display: block;
      padding: 1.5rem 2rem;
      font-size: 24px;
      font-weight: 500;
      color: color("color-1", "darken-1");
      background-color: color("grey", "lighten-4");
    }
  }
}

.notice-view {
  display: flex;
  flex-flow: column;
  margin-bottom: 2rem;

  .notice-title {
    border-top: 2px solid color("color-1", "darken-1");
    border-bottom: 1px solid $table-border-color;
    padding: 16px 0;
    font-size: 24px;
    font-weight: 500;
  }
  .notice-sum {
    display: flex;
    justify-content: space-between;
    color: #757575;
    padding: 16px 0;
    border-bottom: 1px solid $table-border-color;
  }
  .notice-content {
    padding: 20px 0;
  }
}

.policy-tab {
  display: flex;
  flex: 1;
  text-align: center;
  margin-bottom: 2rem;

  .tab {
    padding: 10px 20px;
    color: color("grey", "darken-4");
    background-color: color("grey", "lighten-4");
    border: 1px solid color("grey", "darken-4");
    margin-left: -1px;
    font-size: 14px;
    &.active {
      font-weight: 500;
      color: #fff;
      background-color: color("grey", "darken-4");
    }
  }
  li:first-child a {
    border-radius: 4px 0 0 4px;
  }
  li:last-child a {
    border-radius: 0 4px 4px 0;
  }
}

.policy-box {
  h5 {
    font-size: 20px;
    margin-top: 2rem;
  }
}

.divi-text {
  position: relative;
  height: 68px;
  margin: 2rem 0 1rem 0;
  h5 {
    padding-right: 15px;
    position: absolute;
    background: #fff;
    font-weight: 400;
    z-index: 1;
  }
  &::after {
    content: "";
    display: block;
    height: 1px;
    width: 100%;
    background: color("grey", "lighten-2");
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    z-index: 0;
  }
}

.modal-check-sign {
  text-align: center;
  span {
    color: color("color-1", "lighten-1");
  }
  .btn {
    display: block;
  }
}
