// Logo
.header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;

  .header-wrap {
    background: color("shades", "white");
    box-shadow: rgb(229, 232, 235) 0px -1px 0px 0px inset;
    background: color("shades", "white");
    border-bottom: 1px solid color("grey", "lighten-2");

    @media #{$medium-and-down} {
      width: 100%;
      .container {
        width: 100%;
      }
    }

    a {
      color: color("shades", "black");
    }
    .brand-logo {
      display: flex;
      min-height: $navbar-height;

      @media #{$medium-and-down} {
        min-height: $navbar-height-mobile;
      }

      .logo-white {
        display: none;
      }

      .logo-dark {
        display: block;
      }

      img {
        width: 100px;
      }
      span {
        font-weight: 800;
      }
    }

    a {
      font-size: 18px;
    }
  }
}
