#pagePay {
  .section-1 {
    padding: 0;
  }

  .title {
    text-align: center;
    font-weight: 700;
    margin-bottom: 2rem;
  }

  .content-wrap {
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    padding: 3rem 0;

    display: flex;
    flex-flow: column;
    flex: 1 0 auto;
    justify-content: center;

    min-height: calc(100vh - #{$navbar-height-mobile});

    @media #{$medium-and-up} {
      min-height: calc(100vh - #{$navbar-height});
    }

    .pay-form {
      .option {
        display: none;
      }

      .form-row {
        margin-bottom: 0;

        .input-field {
          margin-top: 0;
          margin-bottom: 0;
          &.col {
            &:first-child {
              padding-left: 0;
            }
            &:last-child {
              padding-right: 0;
            }
            label {
              left: 16px;
            }
          }
        }
      }
      .input-field {
        input {
          margin-bottom: 0;
        }
        .helper-text {
          position: absolute;
          bottom: 9px;
          left: 16px;
          padding: 0 6px;
          width: 100%;
        }
      }
      .file-field {
        .btn {
          width: 100px;
          border-radius: 4px;
          background-color: color("color-1", "sub");
          color: #000;
          font-size: 16px;
        }
      }
    }
    .btn-wrap {
      margin-top: 3rem;
      .btn {
        width: 100%;
        height: 4rem;
        line-height: 4rem;
        margin-bottom: 10px;
      }
    }
  }
  .forget-info {
    text-align: center;
    margin: 2rem 0;
    padding-bottom: 2rem;
    border-bottom: 1px solid color("grey", "lighten-1");

    p {
      margin-bottom: 6px;
    }
    .find-link {
      display: flex;
      justify-content: center;
      a {
        margin: 0 10px;
      }
    }
  }

  .modal-payment {
    max-width: 100%;

    .modal-title {
      font-size: 1.5rem;
      margin-bottom: 2rem;
    }
    .modal-content {
      max-width: 600px;
      margin: 0 auto;

      .btn {
        width: 100%;
        font-size: 14px;
        height: 44px;
        line-height: 44px;
        letter-spacing: -0.75px;
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    .modal-footer {
      max-width: 600px;
      margin: 0 auto;
      .btn {
        font-size: 16px;
        margin: 0;
        margin-right: 20px;
        height: 44px;
        line-height: 44px;
      }
    }
  }

  table {
    tr {
      font-size: 14px;
      @media #{$medium-and-up} {
        font-size: 16px;
      }
      th {
      }
      td {
        color: color("grey", "darken-1");
      }
    }
  }
}

.select-btn {
  .btn {
    display: block;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    height: auto !important;
    line-height: 1.5 !important;
    font-size: 24px;
    -webkit-box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2) !important;
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2) !important;
  }
}

@media #{$large-and-up} {
  .n-desk {
    display: none !important;
  }
}

.btn-around {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
  .btn {
    width: 45%;
  }
}
