.sidenav {
  .user-view {
    padding: 16px;
    background: #f5f5f5;
  }
  .logo {
    display: flex;
    align-items: center;
    margin-bottom: 1rem;

    img {
      width: 40px;
      margin-right: 20px;
    }
  }

  .side-logo {
    color: #000;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 1px;
    padding: 0 20px;
    img {
      width: 100px;
    }
  }

  .login {
    padding: 0 20px;
    margin-bottom: 1rem;
    display: flex;
    justify-content: space-between;

    .btn {
      font-weight: 300;
      width: 45%;
    }
  }

  .user-info {
    display: flex;
    align-items: center;
    .circle {
      width: 50px !important;
      height: 50px !important;
    }
    .info-text {
      margin-left: 16px;
    }
  }
}
