#pagePolicy {
  .section {
    padding: 60px 0 120px 0;

    .block {
      margin-bottom: 50px;
      padding-bottom: 20px;

      &.boder-bottom {
        border-bottom: 1px solid color("grey", "lighten-2");
      }
    }

    .policy-wrap {
      font-size: 14px;

      h5 {
        font-size: 1.2rem;
        margin-top: 2rem;
      }
      ul {
        li {
          color: color("grey", "darken-1");
          margin-bottom: 1rem;
        }
      }
    }
  }
}
