#pageMypage {
  .section {
    padding: 30px 0 120px 0;
  }

  @media #{$medium-and-down} {
    .row {
      padding-left: 0;
      padding-right: 0;
      .col.first {
        padding-left: 0;
      }
      .col.center {
        padding-left: 0.375rem;
        padding-right: 0.375rem;
      }
      .col.last {
        padding-right: 0;
      }
      .col.s12 {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  .card-panel {
    box-shadow: 0 4px 24px 0 rgba(34, 41, 47, 0.1);
  }

  .main-section-1 {
    @media #{$medium-and-up} {
    }
  }

  .btn-wrap {
    .btn {
      width: 100%;
      height: 55px;
      line-height: 55px;
    }
    .btn-sub {
      margin-top: 10px;
    }
  }
  table {
    thead {
      // background-color: color("grey", "lighten-4");
    }

    tr {
      font-size: 14px;
      @media #{$medium-and-up} {
        font-size: 16px;
      }
      th {
        color: color("grey", "darken-1");
      }
      td {
        color: color("grey", "darken-3");
      }

      &.pay-cancle {
        td {
          color: color("grey", "lighten-1");
          text-decoration: line-through;
        }
      }
    }

    .btn {
      font-size: 12px;
      height: 26px;
      line-height: 26px;
      border-radius: 3px;
      width: 60px;
      padding: 0;

      &.btn-re {
        background-color: color("red", "accent-4");
        color: #fff;
      }
    }
  }
  .modal {
    width: 96%;
    .modal-content {
      padding: 15px;
    }

    table {
      margin-top: 20px;
      th {
        font-size: 14px;
        padding: 10px;
        text-align: left;
        background-color: color("grey", "lighten-3");
        @media #{$medium-and-up} {
          font-size: 16px;
        }
      }
      td {
        padding: 10px;
        font-size: 14px;
        text-align: right;
        @media #{$medium-and-up} {
          font-size: 16px;
        }
      }
    }
    .modal-btn-tween {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 2rem;

      .btn {
        font-size: 16px;
        height: 40px;
        line-height: 40px;
        margin-right: 15px;
        font-weight: 400;
        margin: 0;
      }
    }
  }

  .table-wrap {
    padding: 30px 10px;
    background-color: color("color-1", "lighten-2");
    table {
      thead {
        background-color: transparent;
      }
      th {
        font-size: 12px;
        padding: 10px;
        text-align: center;
        background-color: transparent;
        @media #{$medium-and-up} {
          font-size: 16px;
        }
      }
      td {
        padding: 10px;
        font-size: 12px;
        text-align: center;
        @media #{$medium-and-up} {
          font-size: 16px;
        }
      }
    }
    .modal-close {
      font-size: 14px;
      height: 30px;
      line-height: 30px;
      margin-right: 15px;
      font-weight: 700;
    }
  }

  table {
    &.bg-blue {
      // thead {
      //   background-color: lighten($color: #e3f4fa, $amount: 5);
      // }
      // th {
      //   background-color: lighten($color: #e3f4fa, $amount: 5);
      // }
      // td {
      //   background-color: lighten($color: #e3f4fa, $amount: 5);
      // }
    }
    &.bg-yellow {
      // thead {
      //   background-color: lighten($color: #f9fec7, $amount: 5);
      // }
      // th {
      //   background-color: lighten($color: #f9fec7, $amount: 5);
      // }
      // td {
      //   background-color: lighten($color: #f9fec7, $amount: 5);
      // }
      .check {
        span {
          padding-left: 10px;
          &::before {
            margin: 0;
            // width: 14px;
            // height: 14px;
            top: 6px;
          }
        }
        [type="checkbox"]:checked + span:not(.lever):before {
          top: 4px;
          height: 18px;
        }
      }
    }

    .btn-grey {
      border-radius: 2px;
      background: #777;
      color: #fff;
      font-weight: 400;
      width: 100%;
    }
    .btn-red {
      border-radius: 2px;
      background: color("red", "base");
      color: #fff;
      font-weight: 400;
      width: 100%;
    }
  }

  .btn-sm {
    height: 40px;
    line-height: 40px;
    font-weight: 400;
  }
}

.title-button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

  .btn {
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    border-radius: 3px;
    font-weight: 400;
  }
}
