#pageLogin {
  main {
    height: calc(var(--vh, 1vh) * 100);
    margin-top: 0;
    .h100 {
      padding: 0;
      height: 100%;
      .container {
        height: 100%;
      }
    }
  }

  .title {
    text-align: center;
    font-weight: 700;
    margin-bottom: 2rem;
  }

  .content-wrap {
    max-width: 420px;
    width: 100%;
    margin: 0 auto;
    padding: 3rem 0;
    display: flex;
    flex-flow: column;
    flex: 1 0 auto;
    justify-content: center;
    margin-bottom: $navbar-height-mobile;

    @media #{$medium-and-up} {
      // height: calc(var(--vh, 1vh) * 100 - #{$navbar-height});
    }

    .login-form {
      .input-field {
        .helper-text {
          position: absolute;
          bottom: 9px;
          left: 16px;
          padding: 0 6px;
          width: 100%;
        }
      }
      .text {
        font-size: 14px;
        color: color("grey", "lighten-1");
      }
      .col-block {
        span {
          display: block;
          margin-bottom: 0.5rem;
        }
        .form-row {
          margin-bottom: 0;

          .input-field {
            margin-top: 0;
            margin-bottom: 0;
            &.col {
              &:first-child {
                padding-left: 0;
              }
              &:last-child {
                padding-right: 0;
              }
              &.center {
                font-size: 30px;
                height: 4rem;
                line-height: 4rem;
                margin-bottom: 0;
              }
              label {
                left: 16px;
              }
              &.email {
                input {
                  padding-right: 3rem;
                }
                .label-icon {
                  position: absolute;
                  right: 28px;
                  top: 16px;
                  font-size: 20px;
                  color: color("grey", "lighten-1");
                  margin-bottom: 0;
                }
              }
            }
          }
        }

        .btn {
          width: 100%;
          border-radius: 4px;
          height: $input-height;
          font-size: 16px;
          line-height: 1.25;
          margin-top: 0.25rem;
        }
      }

      .file-field {
        .btn {
          width: 100px;
          border-radius: 4px;
          background-color: color("color-1", "darken-2");
          font-size: 14px;
          padding: 0;
          // word-break: keep-all;
          span {
            display: block;
            line-height: 1.25;
            padding-top: 16px;
          }
        }
      }

      .file-camera {
        background-image: url(../images/icon/icon-camera.png);
        background-repeat: no-repeat;
        background-position: 92% 14px;
        background-size: 36px;

        @media only screen and (min-width: $medium-screen) {
          background-position: 97% 18px;
        }

        @media only screen and (min-width: $large-screen) {
          background-position: 97% 22px;
        }
      }
    }
    .btn-wrap {
      margin-top: 3rem;
      .btn {
        width: 100%;
        height: 4rem;
        line-height: 4rem;
        margin-bottom: 10px;
      }
    }
    .btn {
      width: 100%;
      height: 4rem;
      line-height: 4rem;
    }
  }
  .forget-info {
    text-align: center;
    margin: 2rem 0;
    padding-bottom: 2rem;
    border-bottom: 1px solid color("grey", "lighten-1");

    p {
      margin-bottom: 6px;
    }
    .find-link {
      display: flex;
      justify-content: center;
      a {
        margin: 0 10px;
      }
    }
  }
}
