#pageIndex {
  main {
    margin-top: 0;
  }

  .header-wrap {
    background: transparent;
    box-shadow: none;
    border-bottom: 0;
    // box-shadow: rgb(229, 232, 235) 0px -1px 0px 0px inset;
    a {
      color: color("shades", "black");
    }

    .logo-white {
      display: none;
    }

    .logo-dark {
      display: block;
    }

    &.nav-scroll {
      border-bottom: 1px solid color("grey", "lighten-2");
      transition: all ease-in-out 0.2s;
      background: color("shades", "white");
      a {
        color: color("shades", "black");
        transition: all ease-in-out 0.2s;
      }

      .logo-white {
        display: none;
      }

      .logo-dark {
        display: block;
      }
    }
  }

  .section {
    padding: 30px 0;

    @media #{$medium-and-up} {
      padding: 120px 0;
    }
  }

  .section-1 {
    background-image: url(../images/background/main-bg-01.jpg);
    background-position: center center;
    background-size: cover;
    // height: calc(100vh - #{$navbar-height-mobile});
    min-height: -webkit-fill-available;
    // height: 100vh;

    height: calc(var(--vh, 1vh) * 100);

    position: relative;

    @media #{$medium-and-up} {
      background-image: url(../images/background/main-bg-01-lg.jpg);

      // height: calc(100vh - #{$navbar-height});
    }

    .container {
      display: flex;
      flex-flow: column;
      flex: 1 0 auto;
      align-items: center;
      justify-content: center;
      height: 100%;
      text-align: center;
    }

    .main-top-img {
      @include fadeBottom(1s, 0s);
      margin-bottom: 40px;
      max-width: 50%;

      @media #{$medium-and-up} {
        max-width: 200px;
      }
    }

    .main-title {
      @include fadeRight(1s, 0.3s);
      color: #000;
      font-size: 2.25rem;
      line-height: 1.25;
      font-weight: 700;
      margin-top: 0;
      margin-bottom: 60px;
      padding: 0 10px;
    }

    .main-text {
      color: #000;
      font-size: 1.25rem;
      font-weight: 400;
      line-height: 1.25;
      margin-top: 0;
      margin-bottom: 60px;
      position: relative;
      padding: 0 10px;

      b {
        font-weight: 700;
      }

      &.text-1 {
        @include fadeRight(1s, 0.6s);
      }

      &.text-2 {
        @include fadeRight(1s, 0.9s);
        margin-bottom: 100px;
      }
    }
  }

  .section-2 {
    padding-top: 60px;
    .main-tab {
      max-width: 800px;
      margin: 0 auto;
      box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
        0 1px 10px 0 rgba(0, 0, 0, 0.12), 0 2px 4px -1px rgba(0, 0, 0, 0.3);
    }

    .tabs {
      display: flex;
      margin-top: 2rem;
      margin-bottom: 0;

      .indicator {
        background-color: color("color-1", "sub");
        display: none;
      }

      .tab {
        display: flex;
        flex: 1;
        width: 50%;

        @media #{$extra-small-and-down} {
          width: auto;
        }

        @media #{$medium-and-up} {
          width: 49%;
        }

        a {
          width: 100%;
          display: block;
          font-size: 18px;
          color: color("grey", "darken-4");
          background: color("grey", "lighten-4");
          z-index: 0;

          &.active {
            font-weight: 700;
            background: color("color-1", "lighten-2");
            z-index: 1;
            box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.14),
              0 1px 10px 0 rgba(0, 0, 0, 0.12),
              0 2px 4px -1px rgba(0, 0, 0, 0.3);
          }
        }
      }
    }
    .tabbox {
      background: color("color-1", "lighten-2");
      padding: 30px 20px;
    }

    .img-list-2 {
      margin: 0;
      li {
        position: relative;
        padding-left: 40px;
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }

        img {
          display: inline-block;
          width: 16px;
          position: absolute;
          left: 10px;
          top: 5px;
        }
      }
    }
  }

  .section-3 {
    text-align: center;

    .chart-img {
      margin: 30px 0 20px 0;
      max-width: 400px;
      margin: 0 auto;
    }

    @media #{$medium-and-up} {
      .row {
        display: flex;

        .col:last-child {
          padding-left: 100px;
        }
      }
    }

    .reference-box {
      margin-top: -2rem;
      padding: 20px;
      color: color("grey", "lighten-1");
      background-color: color("grey", "lighten-5");
      border-radius: 5px;
      font-size: 12px;
      text-align: left;

      @media #{$medium-and-up} {
        font-size: 14px;
      }

      ul {
        li {
          list-style: disc;
          margin-left: 20px;
        }
      }
    }
  }

  .section-4 {
    text-align: center;

    .icon-box {
      margin-top: 2rem;
      padding: 2.5rem 12px;
      background: color("color-1", "lighten-2");

      @media #{$medium-and-up} {
        margin-top: 4rem;
      }

      .item {
        margin-bottom: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: left;
        &:last-child {
          margin-bottom: 0;
        }
        .img-wrap {
          width: 100px;
          padding-right: 20px;

          img {
            width: 100%;
          }
        }
        .text-wrap {
          flex: 1;
          .sm-text {
            display: block;
            color: color("red", "base");
            font-size: 14px;
          }
        }
      }
    }
  }

  .section-5 {
    text-align: center;

    .row {
      padding-left: 0;
      padding-right: 0;
      .col.first {
        padding-left: 0;
      }
      .col.center {
        padding-left: 0.375rem;
        padding-right: 0.375rem;
      }
      .col.last {
        padding-right: 0;
      }
    }

    .logo-box {
      margin-top: 2rem;
      margin-bottom: 2rem;

      .card-wrap {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        .col {
          width: calc(100% / 3);
          @media #{$medium-and-up} {
            width: calc(100% / 5);
            margin-bottom: 10px;
          }
        }
        // .col:nth-child(1) img {
        //   width: 100px;
        // }
        // .col:nth-child(2) img {
        //   width: 100px;
        // }
        // .col:nth-child(3) img {
        //   width: 100px;
        // }
        // .col:nth-child(4) img {
        //   width: 140px;
        // }
        // .col:nth-child(5) img {
        //   width: 100px;
        // }
        // .col:nth-child(6) img {
        //   width: 100px;
        // }
        // .col:nth-child(7) img {
        //   width: 100px;
        // }
        // .col:nth-child(8) img {
        //   width: 100px;
        // }
        // .col:nth-child(9) img {
        //   width: 100px;
        // }
        // .col:nth-child(10) img {
        //   width: 100px;
        // }
      }

      .img-wrap {
        margin-bottom: 20px;
      }

      img {
        width: 100%;
        max-width: 100px;

        @media #{$medium-and-up} {
          max-width: 110px;
        }
      }

      span {
        display: block;
      }
    }

    .pay-box {
      margin-top: 2rem;
      margin-bottom: 2rem;

      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 60px;
        margin: 0 10px;
        border-radius: 4px;
      }
    }

    hr {
      margin-top: 4rem;
      margin-bottom: 4rem;
      border: 0;
      height: 1px;
      background: color("grey", "lighten-2");
    }

    .bank-wrap {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
      margin-top: 2rem;
      margin-bottom: 2rem;

      .col {
        width: calc(100% / 3);
        margin-bottom: 30px;

        @media #{$medium-and-up} {
          width: calc(100% / 5);
          margin-bottom: 0px;
        }

        img {
          width: 100%;
          max-width: 100px;
          display: block;
        }
      }
    }
    .img-kcp {
      margin-top: 2rem;
      max-width: 100px;
    }
  }

  .section-6 {
    background-color: color("grey", "lighten-5");
    text-align: center;
    img {
      margin-top: 2rem;
      max-width: 100px;
    }
  }

  .section-7 {
    .container {
      max-width: 520px;
    }

    .form {
      margin-top: 2rem;

      .btn-contact {
        width: 100%;
        background-color: color("color-1", "lighten-1");
        font-size: 18px;
      }
    }
  }

  h5 {
    line-height: 1.5;
  }

  .flow-btn {
    width: 90%;
    font-size: 24px;
    margin-top: 2rem;
    position: fixed;
    bottom: 20px;
    left: 5%;
    background: color("color-1", "lighten-1");
    color: #fff;
    height: 64px;
    line-height: 64px;
    z-index: 10;

    @media #{$medium-and-up} {
      max-width: 1000px;
      left: 0;
      right: 0;
      margin: auto;
      height: 56px;
      line-height: 56px;
    }
  }

  .footer-copyright {
    padding-bottom: 110px;
  }
}
