#pageSignup {
  .section-1 {
    padding: 0;
  }
  .sign-message {
    text-align: center;

    h5 {
      line-height: 1.5;
      span {
        color: color("color-1", "lighten-1");
      }
    }
    .weight-400 {
      font-weight: 400;
    }
  }

  .sign {
    padding: 0;
    .content-wrap {
      padding-top: 0;
      padding-bottom: 0;
      min-height: calc(100vh - 0px);
      margin-top: -56px;
      @media #{$small-and-down} {
      }
    }
  }

  .title {
    text-align: center;
    font-weight: 700;
    margin-bottom: 2rem;
  }

  .content-wrap {
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    padding: 3rem 0;

    display: flex;
    flex-flow: column;
    flex: 1 0 auto;
    justify-content: center;

    min-height: calc(100vh - #{$navbar-height-mobile});

    @media #{$medium-and-up} {
      min-height: calc(100vh - #{$navbar-height});
    }

    .sign-form {
      .input-field {
        .helper-text {
          position: absolute;
          bottom: 9px;
          left: 16px;
          padding: 0 6px;
          width: 100%;
        }
      }
      .text {
        font-size: 14px;
        color: color("grey", "lighten-1");
      }
      .col-block {
        span {
          display: block;
          margin-bottom: 0.5rem;
        }
        .form-row {
          margin-bottom: 0;

          .input-field {
            margin-top: 0;
            margin-bottom: 0;
            &.col {
              &:first-child {
                padding-left: 0;
              }
              &:last-child {
                padding-right: 0;
              }
              &.center {
                font-size: 30px;
                height: 4rem;
                line-height: 4rem;
                margin-bottom: 0;
              }
              label {
                left: 16px;
              }
              &.email {
                input {
                  padding-right: 3rem;
                }
                .label-icon {
                  position: absolute;
                  right: 28px;
                  top: 16px;
                  font-size: 20px;
                  color: color("grey", "lighten-1");
                  margin-bottom: 0;
                }
              }
            }
          }
        }

        .btn {
          width: 100%;
          border-radius: 4px;
          height: $input-height;
          font-size: 16px;
          line-height: 1.25;
          margin-top: 0.25rem;
        }
      }

      .file-field {
        .btn {
          width: 100px;
          border-radius: 4px;
          background-color: color("color-1", "darken-2");
          font-size: 14px;
          padding: 0;
          // word-break: keep-all;
          span {
            display: block;
            line-height: 1.25;
            padding-top: 16px;
          }
        }
      }

      .file-camera {
        background-image: url(../images/icon/icon-camera.png);
        background-repeat: no-repeat;
        background-position: 92% 14px;
        background-size: 36px;

        @media only screen and (min-width: $medium-screen) {
          background-position: 97% 18px;
        }

        @media only screen and (min-width: $large-screen) {
          background-position: 97% 22px;
        }
      }
    }
    .btn-wrap {
      margin-top: 3rem;
      .btn {
        width: 100%;
        height: 4rem;
        line-height: 4rem;
        margin-bottom: 10px;
      }
    }
  }
  .forget-info {
    text-align: center;
    margin: 2rem 0;
    padding-bottom: 2rem;
    border-bottom: 1px solid color("grey", "lighten-1");

    p {
      margin-bottom: 6px;
    }
    .find-link {
      display: flex;
      justify-content: center;
      a {
        margin: 0 10px;
      }
    }
  }

  .scroll-box {
    border: 1px solid color("grey", "lighten-1");
    padding: 10px;
    border-radius: 4px;
    height: 150px;
    overflow: auto;
    p {
      font-size: 12px;
      margin: 0;
    }
  }
  .modal {
    .check-wap {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-bottom: 10px;
      h5 {
        font-size: 18px;
        font-weight: 700;
        margin: 0;
      }
      .switch {
        label {
          .lever {
            margin: 0;
          }
        }
      }
    }
    .scroll-box {
      margin-bottom: 2rem;
      box-sizing: border-box;
      background-color: color("grey", "lighten-4");

      p {
        font-size: 12px;
      }
      li {
        font-size: 12px;
        margin-bottom: 3px;
        ul {
          margin-top: 3px;
        }
      }
      h5 {
        font-size: 12px;
      }
    }
    .btn {
      display: block;
      width: calc(100% - 48px);
      margin: 0 auto;
      margin-bottom: 10px;
      font-size: 15px;
    }
  }

  .modal-check {
    width: 90%;
    max-height: 90%;
    .modal-title {
      font-size: 1.5rem;
      margin-bottom: 2rem;
    }
  }
}

.sign-btn-list {
  a {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    line-height: 60px;
    border-radius: 4px;
    margin-bottom: 6px;
    font-weight: 400;
    position: relative;
    font-size: 16px;

    img {
      position: absolute;
      left: 20px;
    }

    &.btn {
      &-naver {
        background: #03c75a;

        img {
          width: 16px;
        }
      }
      &-kakao {
        background: #fee500;
        color: #191600;

        img {
          width: 22px;
        }
      }
      &-facebook {
        background: #3c5a9a;

        img {
          width: 30px;
          left: 15px;
        }
      }
      &-google {
        background: #f5f5f5;
        color: #191600;

        img {
          width: 22px;
        }
      }
    }

    &.btn-social {
      img {
        margin-right: 20px;
        width: 26px;
      }
      justify-content: flex-start;
      background: #fff;
      color: color("grey", "darken-4");

      border-radius: 2px;
      border: 1px solid color("color-1", "lighten-1");
    }
  }
}
