#pageService {
  .section {
    padding: 0px 0 30px 0;

    .container {
      max-width: 600px;
    }
  }

  @media #{$medium-and-down} {
    .row {
      padding-left: 0;
      padding-right: 0;
      .col.first {
        padding-left: 0;
      }
      .col.center {
        padding-left: 0.375rem;
        padding-right: 0.375rem;
      }
      .col.last {
        padding-right: 0;
      }
      .col.s12 {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }

  .section-list {
    h5 {
      margin-bottom: 2rem;
    }

    .img-list-3 {
      li {
        color: color("grey", "darken-2");
        border: 1px solid color("grey", "lighten-2");
        border-radius: 4px;
        margin-bottom: 10px;
        padding: 15px 15%;

        display: flex;
        justify-content: center;
        flex-flow: column;
        align-items: center;

        img {
          width: 40px;
          margin-bottom: 5px;
        }
        span {
          color: color("grey", "darken-1");
          text-align: center;
        }
      }
    }
  }

  .section-1 {
    padding: 60px 0;
    .container {
      h5 {
        line-height: 1.3;
        font-weight: 700;
      }
      h6 {
        line-height: 1.3;
        color: color("grey", "darken-2");
      }
    }
  }

  .section-2 {
    .container {
    }
  }

  .section-3 {
    .container {
    }
    .img-list-3 {
      li {
        background-color: #fff;
        border: 0;
      }
    }
  }

  .section-list {
    .list {
      background-color: color("grey", "lighten-5");
      padding: 24px 30px;
      .title {
        color: color("color-1", "lighten-1");
        font-weight: 400;
        font-size: 18px;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 10px;

        &::before {
          display: none;
        }
      }
      li {
        color: color("grey", "darken-1");
        margin-bottom: 6px;
        padding-left: 14px;
        position: relative;

        &:last-child {
          margin-bottom: 0;
        }

        &::before {
          content: "";
          display: inline-block;
          width: 6px;
          height: 6px;
          border-radius: 6px;
          position: absolute;
          top: 8px;
          left: 0px;
          background: color("grey", "lighten-1");
        }
      }
    }
  }

  .flow-btn {
    width: 90%;
    font-size: 24px;
    margin-top: 2rem;
    position: fixed;
    bottom: 20px;
    left: 5%;
    z-index: 10;

    @media #{$medium-and-up} {
      max-width: 1000px;
      left: 0;
      right: 0;
      margin: auto;
      height: 50px;
      line-height: 50px;
    }
  }
  .footer-copyright {
    // padding-bottom: 110px;
  }
}
